import React from 'react';
import { View, Text, Button, StyleSheet, ScrollView } from 'react-native';
import { useTheme } from '../theme/ThemeContext';

export interface GameManagementProps {
    gameId: string;
    onAddAgent: () => void;
    onStartGame: () => void;
    playerCount: number;
    players: Array<{
        id: string;
        name: string;
        isAgent: boolean;
        isOwner?: boolean;
    }>;
    isOwner: boolean;
    onKickPlayer: (playerId: string) => void;
    gameCharacteristics: {
        minPlayers: number;
        maxPlayers: number;
        playerGroupSize: number;
        extraActions: string[];
        gamePointsReversed: boolean;
    };
}

export const GameManagement: React.FC<GameManagementProps> = ({
    gameId,
    onAddAgent,
    onStartGame,
    playerCount,
    players,
    isOwner,
    onKickPlayer,
    gameCharacteristics
}) => {
    const { colors } = useTheme();
    
    const styles = StyleSheet.create({
        container: {
            flex: 1,
            padding: 20,
        },
        subtitle: {
            fontSize: 18,
            fontWeight: 'bold',
            marginBottom: 10,
        },
        controls: {
            flexDirection: 'column',
            justifyContent: 'space-around',
            marginTop: 20,
        },
        playerListContainer: {
            marginTop: 10,
            marginBottom: 10,
            height: 160, // Fixed height for about 4 players without scrolling
            borderWidth: 1,
            borderColor: '#ddd',
            borderRadius: 5,
        },
        playerList: {
            flex: 1,
        },
        playerRow: {
            flexDirection: 'row',
            justifyContent: 'space-between',
            paddingVertical: 8,
            paddingHorizontal: 10,
            borderBottomWidth: 1,
            borderBottomColor: '#eee',
        },
        playerName: {
            fontSize: 16,
        },
        kickButton: {
            color: 'red',
            fontSize: 18,
            fontWeight: 'bold',
            padding: 5,
        },
        buttonSpacer: {
            marginTop: 10,
        }
    });

    const hasEnoughPlayers = playerCount >= (gameCharacteristics?.minPlayers || 2);
    
    return (
        <View style={styles.container}>
            <Text style={[styles.subtitle, { color: colors.titleText }]}>Game Setup</Text>
            <Text style={{ color: colors.statusText }}>Players: {playerCount} (Min: {gameCharacteristics?.minPlayers || 2})</Text>
            
            <View style={styles.playerListContainer}>
                <ScrollView style={styles.playerList}>
                    {players.map(player => (
                        <View key={player.id} style={styles.playerRow}>
                            <Text style={[styles.playerName, { color: colors.statusText }]}>
                                {player.name} {player.isAgent ? '(AI)' : ''} {player.isOwner ? '(Owner)' : ''}
                            </Text>
                            {isOwner && !player.isOwner && (
                                <Text 
                                    style={styles.kickButton}
                                    onPress={() => onKickPlayer(player.id)}
                                >
                                    
                                </Text>
                            )}
                        </View>
                    ))}
                </ScrollView>
            </View>
            
            <View style={styles.controls}>
                {isOwner && (
                    <>
                        <Button title="Add AI Player" onPress={onAddAgent} />
                        {hasEnoughPlayers && (
                            <View style={styles.buttonSpacer}>
                                <Button title="Start Game" onPress={onStartGame} />
                            </View>
                        )}
                    </>
                )}
            </View>
        </View>
    );
};