import React, { useState } from 'react';
import { View, TextInput, Button, StyleSheet, Text, Alert } from 'react-native';
import { SocketService } from '../services/socket.service';
import { useTheme } from '../theme/ThemeContext';

export const LoginScreen: React.FC<{ navigation: any }> = ({ navigation }) => {
    const [username, setUsername] = useState('');
    const socketService = SocketService.getInstance();
    const { colors } = useTheme();

    const handleLogin = async () => {
        if (!username.trim()) {
            Alert.alert('Error', 'Please enter a username');
            return;
        }

        try {
            console.log('Logging in with username:', username);
            const response = await socketService.login(username);
            if (response.success) {
                // Store the username globally in the SocketService
                socketService.setCurrentUser(username, response.userId!);
                console.log('Login successful. Navigating to Home.');
                navigation.replace('Home'); // Using replace to prevent going back to login
            } else {
                console.log('Login failed:', response.error || 'Username already taken');
                Alert.alert('Login Failed', response.error || 'Username already taken');
            }
        } catch (error) {
            Alert.alert('Error', 'Failed to connect to server');
        }
    };

    const styles = StyleSheet.create({
        container: {
            flex: 1,
            padding: 20,
            justifyContent: 'center',
            backgroundColor: colors.containerBackground,
        },
        title: {
            fontSize: 24,
            textAlign: 'center',
            marginBottom: 30,
            color: colors.titleText,
        },
        input: {
            borderWidth: 1,
            borderColor: colors.inputBorder,
            backgroundColor: colors.inputBackground,
            padding: 10,
            marginBottom: 20,
            borderRadius: 5,
            color: colors.titleText,
        },
        buttonContainer: {
            marginTop: 10,
        }
    });

    return (
        <View style={styles.container}>
            <Text style={styles.title}>Enter Your Username</Text>
            <TextInput
                style={styles.input}
                onChangeText={setUsername}
                value={username}
                placeholder="Username"
                placeholderTextColor={colors.playerStatsText}
                autoCapitalize="none"
                autoCorrect={false}
            />
            <View style={styles.buttonContainer}>
                <Button 
                    title="Login" 
                    onPress={handleLogin} 
                    color={colors.primaryButton}
                />
            </View>
        </View>
    );
}; 